import { Avatar, styled } from '@mui/material'
import React from 'react'

const LeftContent = () => {
 
 const SignAvavtar = styled(Avatar)({
  width:'80%',
  height:'auto',
  margin:'auto',
  alignItems:'center',
  display:'flex',
  justifyContent:'center',
  marginBlockStart:'6%'  
 })

    
  return <SignAvavtar src={require('../../assets/image/signin.png')} variant='square' alt='logo'/>
}

export default LeftContent