import { Avatar, Box, Button, Checkbox, Divider, FormControl, FormControlLabel, InputAdornment, TextField, Typography, styled, useMediaQuery, useTheme } from '@mui/material';
import Grid from '@mui/material/Grid';
import React from 'react'
import LeftContent from './leftContent';
import MailOutlineOutlinedIcon from '@mui/icons-material/MailOutlineOutlined';
import { useFormik } from 'formik';
import * as Yup from 'yup';
import OutlinedInput from '../../components/input/outlinedInput';
import CancelRoundedIcon from '@mui/icons-material/CancelRounded';
import CheckCircleOutlineRoundedIcon from '@mui/icons-material/CheckCircleOutlineRounded';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import WarningAmberRoundedIcon from '@mui/icons-material/WarningAmberRounded';

const SignIn = () => {
 const theme = useTheme() 
 const mediaQuery = useMediaQuery('(min-width:720px)')

 const initialValues = {
   email : '',
   password:'',
 }

 const validationSchema = Yup.object().shape({
  email: Yup.string().email('Invalid email').required('Required'),
  password: Yup.string().min(8, 'Password must be at least 8 characters').required('Required')
});
 
 const formik = useFormik({
  initialValues:initialValues,
  validationSchema: validationSchema
 })

 const handleClick = () => {
  window.location.href = "https://accounts.google.com/v3/signin/identifier?continue=https%3A%2F%2Fmail.google.com%2Fmail%2Fu%2F0%2F&emr=1&followup=https%3A%2F%2Fmail.google.com%2Fmail%2Fu%2F0%2F&ifkv=AS5LTASZBhTyGRQgy-ML-l_d2dEEN2PBhoww-gqB913y_k7TpnU-sAkglAdvKvJIh-WaRA2jeKg3YQ&osid=1&passive=1209600&service=mail&flowName=GlifWebSignIn&flowEntry=ServiceLogin&dsh=S-1744538246%3A1719389745641883&ddm=0";
 };

 const CustBox = styled(Box)({
  borderWidth:'1.6px',
  borderColor: theme.palette.grey[50],
  borderStyle: 'solid',
  paddingBlock:'5px',
  paddingInline:'12px',
  borderRadius:'4px',
  display:'flex',
  justifyContent:'center',
  marginBlock:'16px',
  cursor:'pointer',
  alignItems:'center',
  '&:hover':{
    borderColor: theme.palette.grey[200], 
  }
 })

  return (
    <div className='relative w-full h-screen flex justify-center items-center sm: p-3' style={{backgroundColor:theme?.palette?.secondary?.light}}>
      <Grid container item xs={12} className=''>
       {mediaQuery ? <Grid item sm={6} className='hidden md:block'>
          <LeftContent />
        </Grid> : null}
        <Grid item xs={12} sm={6} md={6} alignSelf={"center"} paddingInline={mediaQuery ? 10 : 0} padding={mediaQuery ? 0 :1.5}>
          <div className='w-full sm:w-80'>
            <Typography fontWeight={600} fontSize={22}>
              Hi, Welcome back
            </Typography>
            <Typography fontWeight={500} fontSize={12} lineHeight={1}>
              Login to you account to enjoy
            </Typography>

            <CustBox onClick={handleClick}>
              <Avatar
                src={require("../../assets/image/search.png")}
                alt='logo'
                sx={{ width: 15, height: "auto" }}
              />
              <Typography fontWeight={500} fontSize={14} ml={1}>
                Or Sign in with google
              </Typography>
            </CustBox>

            <Divider>
              <Typography fontSize={12} fontWeight={500}>
                Or sign in with email
              </Typography>
            </Divider>

            <FormControl fullWidth>
              <Typography fontSize={13} fontWeight={500} mb={0.3}>
                Email
              </Typography>
              <OutlinedInput
                id='email'
                placeholder='Enter the email'
                value={formik?.values?.email}
                onChange={formik?.handleChange}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                     <MailOutlineOutlinedIcon sx={{color:theme.palette.grey[50]}}/>
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment position="end">
                     {Object.values(formik?.errors).length == 0 && formik?.values?.email == '' ? null : formik?.errors?.email ? 
                     <WarningAmberRoundedIcon sx={{color:'red'}}/> : <CheckCircleOutlineRoundedIcon sx={{color:'green'}}/> } 
                    </InputAdornment>
                  ),
                }}
              />
            </FormControl>

            <FormControl fullWidth sx={{ mt: 1 }}>
              <Typography fontSize={13} fontWeight={500} mb={0.3}>
                Password
              </Typography>
              <OutlinedInput
                id='password'
                placeholder='Enter the password'
                value={formik?.values?.password}
                type="password"
                onChange={formik?.handleChange}               
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                     <LockOutlinedIcon sx={{color:theme.palette.grey[50]}}/>
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment position="end">
                     {Object.values(formik?.errors).length == 0 && formik?.values?.password == '' ? null : 
                     formik?.errors?.password ? <WarningAmberRoundedIcon sx={{color:'red'}}/> : <CheckCircleOutlineRoundedIcon sx={{color:'green'}}/> } 
                    </InputAdornment>
                  ),
                }}
              />
            </FormControl>
            
            <div style={{display:'flex', justifyContent:'space-between', alignItems:'center'}}>
             <FormControlLabel control={<Checkbox size='small' defaultChecked />} label="Remember Me" 
             sx={{
              '& .MuiFormControlLabel-label': {
                fontSize: '12px',
                fontWeight:400,
              },
            }}/> 
             
             <Typography fontWeight={500} sx={{cursor:'pointer'}} color={theme.palette.primary.dark} fontSize={12}> Forget password </Typography>

            </div>

             <div style={{display:'flex', justifyContent:'space-between', alignItems:'center'}}>
             <Button style={{
              backgroundColor:theme?.palette?.primary?.main,
              color:theme?.palette?.common?.white,
              borderRadius:'6px',
              paddingBlock:'4px',
              width:'120px'}}>
              Sign In
             </Button>
             <div style={{width:'80px'}}><Divider><Typography fontSize={12} fontWeight={500}>Or </Typography></Divider></div>
              <Typography fontWeight={600} sx={{cursor:'pointer'}} color={theme.palette.primary.dark} fontSize={14}>Sign Up</Typography>

             </div>

          </div>
        </Grid>
      </Grid>
    </div>
  );
}

export default SignIn